<template>
  <div class="tamanho">
    <v-row>
      <v-col cols="12">
        <h1 class="text-center text-h1 py-3">Contato</h1>
        <v-divider class="my-4"></v-divider>
      </v-col>
    </v-row>
    <v-row >
      <v-col class="d-inline-flex justify-center" v-for="(icon, i) in icons"
            :key="i">
          <v-btn
            dark
            class="margin"
            icon
            @click.stop="goRoute(icon.link)"
          >
            <v-icon
              :size="icon.size"
              @mouseenter="icon.size = '80px'"
              @mouseleave="icon.size = '70px'"
              :elevation="8"
            >{{ icon.icon }}</v-icon>
          </v-btn>
      </v-col>
    </v-row>
    <v-row class="d-flex justify-center px-5">
      <v-col md="7" sm="10" cols="12">
          <v-card color="rgb(92, 90, 90, 0)">
            <v-form
              id="contactform"
              v-model="valid"
              lazy-validation
              class="px-5 py-5"
              action="https://formsubmit.io/send/0ae5d0fe-d330-48ba-815b-bf5e3f25830b"
              method="POST"
            >
              <input
                name="_redirect"
                type="hidden"
                id="name"
                value="http://localhost:8081/#/contact?status='enviado'"
              />
              <v-text-field
                v-model="name"
                :counter="50"
                :rules="nameRules"
                label="Seu Nome"
                required
                color="rgb(224, 223, 223)"
                id="name"
                name="name"
              ></v-text-field>

              <v-text-field
                v-model="email"
                :rules="emailRules"
                label="E-mail"
                color="rgb(224, 223, 223)"
                required
                name="email"
                id="email"
              ></v-text-field>

              <v-textarea
                filled
                auto-grow
                label="O que você precisa?"
                rows="5"
                row-height="20"
                color="rgb(224, 223, 223)"
                class="mt-5"
                name="comment"
                id="comment"
              ></v-textarea>
              <input name="_formsubmit_id" type="text" style="display:none" />
              <v-btn :disabled="!valid" dark class="mr-4" @click="validate" type="submit">Enviar</v-btn>

              <v-btn dark class="mr-4" @click="reset">Apagar Tudo</v-btn>
            </v-form>
          </v-card>
        <v-snackbar
          v-model="snackbar"
          :timeout="4000"
          color="green"
          absolute
          right
          rounded="pill"
          top
        >Email enviado com sucesso!</v-snackbar>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  data: () => ({
    valid: true,
    snackbar: false,
    name: "",
    nameRules: [
      (v) => !!v || "Seu nome é importante",
      (v) => (v && v.length <= 50) || "Maximo de 50 caracteres",
    ],
    email: "",
    emailRules: [
      (v) => !!v || "Informe um Email",
      (v) => /.+@.+\..+/.test(v) || "Informe um Email Válido",
    ],
    icons: [
      {
        icon: "mdi-facebook",
        size: "70px",
        link: "https://www.facebook.com/cicerofahning",
      },
      {
        icon: "mdi-linkedin",
        size: "70px",
        link: "https://www.linkedin.com/in/cicerofahning/",
      },
      { icon: "mdi-github", size: "70px", link: "https://github.com/fahning/" },
    ],
  }),
  beforeCreate() {
    if (
      window.location.href ==
      "http://localhost:8081/#/contact?status=%27enviado%27"
    ) {
      this.snackbar = true;
    }
  },
  methods: {
    validate() {
      this.$refs.form.validate();
    },
    reset() {
      this.$refs.form.reset();
    },
    goRoute(route) {
      window.open(route);
    },
    sucesso() {
      alert("Enviado");
    },
  },
};
</script>

<style>
.tamanho {
  width: 100%;
}
.exibeContato {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.margin {
  margin-bottom: 40px;
  margin-top: 20px;
}
</style>