<template>
<div style="min-width:33%; display: flex; justify-content: center;">
    <v-card class="skill rounded " :elevation="8" >
        <div class="text-center">
            <v-progress-circular
            :rotate="360"
            :size="150"
            :width="15"
            :value="value"
            color="rgb(201, 197, 197)"
            >
            <span class="showValue">{{ value }}%</span>
            </v-progress-circular>
            <v-chip
              class="mb-4 nameSkill"
              color="rgb(201, 197, 197)"
              outlined
            >
             {{nameSkill}}
            </v-chip>

        </div>
    </v-card>
</div>
</template>

<script>
export default {
  props:['nameSkill', 'maxValue'],
    data () {
      return {
        interval: {},
        value: 0,
      }
    },
    beforeDestroy () {
      clearInterval(this.interval)
    },
    mounted () {
      this.interval = setInterval(() => {
        this.value = this.maxValue
      }, 1000)
    },
  
}
</script>

<style scoped>

.v-progress-circular {
  margin: 1rem;
}

.skill{
    width: 230px;
    margin: 20px;
    background-color: rgba(119, 118, 118);
}

.nameSkill{
  margin: 0;
  font-size: 20px;
  font-weight: bolder;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.showValue{
    font-size: 30px;
    font-weight: bold;
}

</style>